import {
    USER_DELETE_REQUEST,USER_DELETE_SUCCESS, USER_DETAILS_FAIL,
    USER_DETAILS_REQUEST, USER_DETAILS_RESET, USER_DETAILS_SUCCESS,
    USER_LIST_FAIL, USER_LIST_REQUEST, USER_LIST_REST, USER_LIST_SUCCESS,
    USER_LOGINOUT, USER_LOGIN_FAIL, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS,
    USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS,
    USER_UPDATE_FAIL, USER_UPDATE_REQUEST, USER_UPDATE_SUCCESS,
    USER_UPDATE_PROFILE_FAIL, USER_UPDATE_PROFILE_REQUEST, USER_UPDATE_PROFILE_SUCCESS,
} from "../constContent/userContents";
import axios from 'axios';
import { ORDER_LIST_MY_RESET } from "../constContent/orderContents";

//user login action
export const login = (email, password) => async (dispatch) =>{ 
    try {
        dispatch({
            type: USER_LOGIN_REQUEST
        })
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const { data } = await axios.post(
            '/api/users/login',
            { email, password },
            config
        );

        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })
        //save user info
        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) { 
        dispatch({
            type: USER_LOGIN_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message
                : error.message
        })
    }
}

//user exit action
export const logout = () => async (dispatch) => { 
    localStorage.removeItem('userInfo');
    localStorage.removeItem('shippingAddress')
    dispatch({
        type: USER_LOGINOUT
    })
    dispatch({
        type: USER_LIST_REST
    })
    dispatch({
        type: USER_DETAILS_RESET
    })
    dispatch({
        type: ORDER_LIST_MY_RESET
    })
    // document.location.href = '/login';
}

//user register
export const register = (name, email, password) => async (dispatch) =>{ 
    try {
        dispatch({
            type: USER_REGISTER_REQUEST
        })
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        const { data } = await axios.post(
            '/api/users',
            { name, email, password},
            config
        );

        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data
        })
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })
        //save user info
        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) { 
        dispatch({
            type: USER_REGISTER_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message
                : error.message
        })
    }
}

//user profile
export const getUserDetails = (id) => async (dispatch, getState) => { 
    try {
        dispatch({
            type: USER_DETAILS_REQUEST
        })

        //gain login-successed user info
        const {userLogin:{userInfo} } = getState()
        
        const config = {
            headers: {
                "Content-Type": 'application/json',
                Authorization: `Bearer ${userInfo.token}`,
            }
        }

        const { data } = await axios.get(`/api/users/${id}`, config)
        dispatch({type: USER_DETAILS_SUCCESS, payload: data})
    } catch (error) { 
        dispatch({
            type: USER_DETAILS_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message : error.message
        })
    }
}


//update user profile
export const updateUserProfile = (user) => async (dispatch, getState) => { 
    try {
        dispatch({
            type: USER_UPDATE_PROFILE_REQUEST
        })

        //gain login-successed user info
        const {userLogin:{userInfo} } = getState()
        
        const config = {
            headers: {
                "Content-Type": 'application/json',
                Authorization: `Bearer ${userInfo.token}`,
            }
        }

        const { data } = await axios.put('/api/users/profile', user, config)
        dispatch({ type: USER_UPDATE_PROFILE_SUCCESS, payload: data })
        dispatch({ type: USER_LOGIN_SUCCESS, payload: data })
        
        localStorage.setItem('userInfo', JSON.stringify(data))
    } catch (error) { 
        dispatch({
            type: USER_UPDATE_PROFILE_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message : error.message
        })
    }
}


//get all users list
