//update single user info --admin
export const userUpdateReducer = (state = { user: {} }, action) => { 
    switch (action.type) { 
        case USER_UPDATE_PROFILE_REQUEST:
            return {
                loading: true
            }
        case USER_UPDATE_SUCCESS:
            return {
                loading: false,
                success: true
            }
        case USER_UPDATE_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        case USER_UPDATE_REST:
            return { user: {} }
        default:
            return state;
    }
}