import axios from "axios"
import {
    ORDER_CREATE_FAIL, ORDER_CREATE_REQUEST, ORDER_CREATE_SUCCESS,
    ORDER_DELIVER_FAIL,
    ORDER_DELIVER_REQUEST,
    ORDER_DELIVER_SUCCESS,
    ORDER_DETAILS_FAIL, ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS,
    ORDER_LIST_FAIL,
    ORDER_LIST_MY_FAIL,
    ORDER_LIST_MY_REQUEST,
    ORDER_LIST_MY_SUCCESS,
    ORDER_LIST_REQUEST,
    ORDER_LIST_SUCCESS,
    ORDER_PAY_FAIL,
    ORDER_PAY_REQUEST,
    ORDER_PAY_SUCCESS
} from "../constContent/orderContents"
import { logout} from '../actions/userActions'
import { CART_CLEAR_ITEM } from "../constContent/cartContents"

//create order
export const createOrder = (order) => async (dispatch, getState) => { 
    try {
        dispatch({ type: ORDER_CREATE_REQUEST })
        //gain login-successed user info
        const {userLogin:{userInfo} } = getState()
        
        const config = {
            headers: {
                "Content-Type": 'application/json',
                Authorization: `Bearer ${userInfo.token}`,
            }
        }

        const { data } = await axios.post('/api/orders', order, config)
        dispatch({ type: ORDER_CREATE_SUCCESS, payload: data })
        dispatch({type: CART_CLEAR_ITEM})
    } catch (error) { 
        dispatch({
            type: ORDER_CREATE_FAIL,
            payload: error.response && error.response.data.message ? error.response.data.message : error.message
        })
    }
}

//get order detail
export const getOrderDetails = (id) => async (dispatch, getState) => { 
    try {
        dispatch({ type: ORDER_DETAILS_REQUEST })
        //gain login-successed user info
        const {userLogin:{userInfo} } = getState()
        
        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`,
            }
        }

        const { data } = await axios.get(`/api/orders/${id}`, config)
        dispatch({type: ORDER_DETAILS_SUCCESS, payload: data})
    } catch (error) { 
        dispatch({
            type: ORDER_DETAILS_FAIL,
            payload: error.response && error.response.data.message ? error.response.data.message : error.message
        })
    }
}

//get all orderList
export const listOrders = () => async (dispatch, getState) => {
    try {
        dispatch({ type: ORDER_LIST_REQUEST })

        const { userLogin: { userInfo } } = getState();

        const config = {
            headers: {
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.get('/api/orders', config);
        dispatch({ type: ORDER_LIST_SUCCESS, payload: data})
    } catch (error) { 
        dispatch({
            type: ORDER_LIST_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message : error.message
        })
    }

}

//pay order
export const payOrders = (orderId, paymentResult) => async (dispatch, getState) => {
    try {
        dispatch({ type: ORDER_PAY_REQUEST })

        const { userLogin: { userInfo } } = getState();

        const config = {
            headers: {
                "Content-Type": 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.put(`/api/orders/${orderId}/pay`, paymentResult, config);
        dispatch({ type: ORDER_PAY_SUCCESS, payload: data})
    } catch (error) {
        dispatch({
            type: ORDER_PAY_FAIL,
            payload: error.response && error.response.data.message ?
                error.response.data.message : error.message
        })
    }
}

