import {
    PRODUCT_LIST_FAIL, PRODUCT_LIST_SUCCESS, PRODUCT_LIST_REQUESTS,
    PRODUCT_DETAILS_REQUESTS, PRODUCT_DETAILS_SUCCESS, PRODUCT_DETAILS_FAIL, PRODUCT_DETAILS_RESET,
    PRODUCT_DELETE_REQUESTS, PRODUCT_DELETE_SUCCESS, PRODUCT_DELETE_FAIL,
    PRODUCT_CREATE_REQUESTS, PRODUCT_CREATE_SUCCESS, PRODUCT_CREATE_FAIL, PRODUCT_CREATE_RESET,
    PRODUCT_UPDATE_REQUESTS, PRODUCT_UPDATE_SUCCESS, PRODUCT_UPDATE_FAIL, PRODUCT_UPDATE_RESET,
    PRODUCT_CREATE_REVIEW_REQUESTS, PRODUCT_CREATE_REVIEW_SUCCESS, PRODUCT_CREATE_REVIEW_FAIL, PRODUCT_CREATE_REVIEW_RESET,
    PRODUCT_TOP_REQUESTS, PRODUCT_TOP_SUCCESS, PRODUCT_TOP_FAIL,  
} from "../constContent/productContent"

//get all products
export const productListReducer = (state = { products: [] }, action) => { 
    switch (action.type) { 
        //request products from eerver
        case PRODUCT_LIST_REQUESTS:
            return {
                loading: true,
                products: []
            }
        case PRODUCT_LIST_SUCCESS:
            return {
                loading: false,
                products: action.payload.products,
                pages: action.payload.pages,
                page: action.payload.page// action.payload store data
            }
        case PRODUCT_LIST_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default:
            return state
    }
}

//get single product
export const productDetailsReducer = (state = { product: {} }, action) => { 
    switch (action.type) { 
        case PRODUCT_DETAILS_REQUESTS:
            return {
                loading: true,
                ...state
            }
        case PRODUCT_DETAILS_SUCCESS: 
            return {
                loading: false,
                product: action.payload
            }
        case PRODUCT_DETAILS_FAIL:
            return {
                loading: false,
                payload: action.payload
            }
        case PRODUCT_DETAILS_RESET:
            return {product: {} };
        default:
            return state;
    }
}

//Delte product -- admin
export const productDeleteReducer = (state={}, action) => { 
    switch (action.type) { 
        case PRODUCT_DELETE_REQUESTS:
            return {
                loading: true
            }
        case PRODUCT_DELETE_SUCCESS:
            return {
                loading: false,
                success: true
            }
        case PRODUCT_DELETE_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        default:
            return state;
    }
}

//create product -- admin
export const productCreateReducer = (state = {}, action) => { 
    switch (action.type) { 
        case PRODUCT_CREATE_REQUESTS:
            return {
                loading: true
            }
        case PRODUCT_CREATE_SUCCESS:
            return {
                loading: false,
                success: true,
                product: action.payload
            }
        case PRODUCT_CREATE_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        case PRODUCT_CREATE_RESET:
            return {}
        default:
            return state;
    }
}

//Update product -- admin
export const productUpdateReducer = (state = { product: {} }, action) => { 
    switch (action.type) { 
        case PRODUCT_UPDATE_REQUESTS:
            return {
                loading: true
            }
        case PRODUCT_UPDATE_SUCCESS:
            return {
                loading: false,
                success: true,
                product: action.payload
            }
        case PRODUCT_UPDATE_FAIL:
            return {
                loading: false,
                error: action.payload
            }
        case PRODUCT_UPDATE_RESET:
            return { product: {} }
        default:
            return state;
    }
}

//Create product review
export const productReviewCreateReducer = (state = {}, action) => {
  switch (action.type) {
    case PRODUCT_CREATE_REVIEW_REQUESTS:
      return { loading: true }
    case PRODUCT_CREATE_REVIEW_SUCCESS:
      return { loading: false, success: true }
    case PRODUCT_CREATE_REVIEW_FAIL:
      return { loading: false, error: action.payload }
    case PRODUCT_CREATE_REVIEW_RESET:
      return {}
    default:
      return state
  }
}

//Create product rank
export const productTopRatedReducer = (state = { products: [] }, action) => {
  switch (action.type) {
    case PRODUCT_TOP_REQUESTS:
      return { loading: true, products: [] }
    case PRODUCT_TOP_SUCCESS:
      return {
        loading: false,
        products: action.payload,
      }
    case PRODUCT_TOP_FAIL:
      return { loading: false, error: action.payload }
    default:
      return state
  }
}